.product-index {
    padding-top: 60px;
    width: 100%;
    height: 200px;
    margin-right: 0;
    margin-left: 0;
    background-image: url("/img/resource/product_header_01.jpg");
    &__header {
        color: #fff;
        &--title {
            h1 {
                font-size: 40px;
                margin-top: -13px;
                font-weight: bold;
            }
        }
        &--sub-title {
            font-size: 25px;
            font-weight: bold;
            margin-top: -10px;
        }
        &--description {
            font-size: 15px;
        }
    }
}

.product-content {
    margin-top: 30px;
    margin-bottom: 30px;
    &__main {
        background-color: #242526;
    }
    &__header {
        font-size: 35px;
        font-weight: bold;
        color: #fff;
    }
    &__menu {
        display: block;
        margin-top: 10px;
        &--square {
            width: 12px;
            height: 12px;
            margin-top: 5px;
            margin-left: 2px;
            float: left;
            border: 1px solid #94bf46;
            &:active {
                background-color: #94bf46;
            }
        }
        &--title {
            width: auto;
            margin-left: 25px;
            font-size: 15px;
            a {
                color: #fff;
                text-decoration: none;
                &:hover {
                    font-weight: bold;
                }
            }
        }
        &:hover {
            .product-content__menu--square {
                background-color: #94bf46;
            }
        }
    }
    &__paginate {
        margin-top: 20px;
    }
    &__paginate {
        margin-top: 20px;
    }
    &__category {
        width: 100%;
        background-color: #fff;
        margin-left: 0;
        margin-right: 0;
        margin-bottom: -30px;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 40px;
        a {
            h2 {
                margin-top: 10px;
                margin-bottom: 0;
                color: #000;
                font-size: 18px;
            }
            &:hover {
                text-decoration: none;
                h2 {
                    font-weight: bold;
                }
            }
        }
        .itemheight {
            min-height: 268px;
        }
        &--item {
            width: 200px;
            height: 200px;
            margin: auto;
            margin-top: 20px;
            padding: 0px;
            border: 1px solid #94bf46;
            display: flex;
            justify-content: center;
            align-items: center;
            &:hover {
                background-color: #36b648;
            }
            img {
                width: 100%;
            }
        }
        &--image {
            &-contact, &-line {
                img {
                    height: 60px;
                }
            }
            &-contact {
                text-align: right;
            }
            &-line {
                text-align: left;
            }
        }

        ol.breadcrumb {
            padding: 8px 16px;
            margin-top: 20px;
            list-style: none;
            font-size: 16px;
            font-style: italic;
            background-color: transparent;
        }
        ol.breadcrumb li {
            display: inline;
        }
        ol.breadcrumb li+li:before {
            padding: 8px;
            font-weight: bold;
            color: #626262;
            content: "\003e";
        }
        ol.breadcrumb li a {
            color: #626262;
        }
    }
    hr {
        width: 60px;
        left: 0;
        margin-left: 0;
        border-top: 1px solid #707070;
    }
}

@media screen and (max-width: 767px) {
    .product-content {
        &__category {
            &--image {
                &-contact, &-line {
                    text-align: center;
                    img {
                        height: 50px;
                    }
                    &:last-child {
                        margin-top: 10px;
                    }
                }
            }
        }
    }
}
